import {Component, OnInit, ViewChild} from '@angular/core';
import {ElementRef} from "@angular/core"
declare let BScroll;
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {


  private shops: Array<any>;
  private shanglaShow: boolean = false;

  @ViewChild('hmallhome') myhome: ElementRef;
  constructor() { }

  // 这个钩子会在组件被实例化的时候调用一次，用来初始化组件里面的数据
  ngOnInit() {
    this.shops = [
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      },
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      },
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      },
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      },
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      },
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      },
      {
        "name": '广州弗莱',
        "image": './assets/images/card-pic.png'
      }
    ];
    this.myhome.nativeElement.style.height = window.screen.availHeight + 'px';
    console.log(window.screen.height);
  }

  ngAfterContentInit() {
    console.log(this.myhome.nativeElement);
    let self = this;

    setTimeout(function () {
      new BScroll(self.myhome.nativeElement, {click: true, bounceTime: 1000});
    },50);
  }

}



